import React from 'react';

async function getHealth() {
  const res = await fetch(
    `${process.env.NEXT_PUBLIC_API_URL}/health`,
    { cache: 'no-store' }
  );
  if (!res.ok) {
    throw new Error(`Health check failed: ${res.statusText}`);
  }
  return res.json() as Promise<{ status: string; timestamp: string }>;
}

// Mock summary data for Home Dashboard
const MOCK_SUMMARY = {
  totalAssets: '$2.45M',
  totalClients: 14,
  complianceAlerts: 2,
  riskLevel: 'Moderate',
  lastActivity: '2025-05-01 21:37',
};

export default async function Home() {
  let health: { status: string; timestamp: string };
  try {
    health = await getHealth();
  } catch (err: any) {
    health = { status: 'error', timestamp: err.message };
  }

  return (
    <main className="p-8 max-w-5xl mx-auto space-y-8">
      {/* Hero */}
      <div className="mb-8">
        <h1 className="text-4xl font-bold text-gray-900 mb-2">HighWater Protocol Dashboard</h1>
        <p className="text-gray-600 text-lg">Your digital asset intelligence and compliance hub</p>
      </div>

      {/* Summary KPIs */}
      <section className="grid grid-cols-2 md:grid-cols-4 gap-6">
        <div className="bg-white rounded shadow p-6 flex flex-col items-center">
          <span className="text-2xl font-bold text-blue-700">{MOCK_SUMMARY.totalAssets}</span>
          <span className="text-gray-500 text-sm mt-1">Total Assets</span>
        </div>
        <div className="bg-white rounded shadow p-6 flex flex-col items-center">
          <span className="text-2xl font-bold text-green-700">{MOCK_SUMMARY.totalClients}</span>
          <span className="text-gray-500 text-sm mt-1">Clients</span>
        </div>
        <div className="bg-white rounded shadow p-6 flex flex-col items-center">
          <span className="text-2xl font-bold text-yellow-600">{MOCK_SUMMARY.complianceAlerts}</span>
          <span className="text-gray-500 text-sm mt-1">Compliance Alerts</span>
        </div>
        <div className="bg-white rounded shadow p-6 flex flex-col items-center">
          <span className="text-2xl font-bold text-red-600">{MOCK_SUMMARY.riskLevel}</span>
          <span className="text-gray-500 text-sm mt-1">Risk Level</span>
        </div>
      </section>

      {/* Recent Activity */}
      <section className="bg-white rounded shadow p-6">
        <h2 className="text-xl font-semibold mb-2 text-gray-800">Recent Activity</h2>
        <ul className="list-disc ml-6 text-gray-700">
          <li>Portfolio rebalanced - {MOCK_SUMMARY.lastActivity}</li>
          <li>Compliance report generated - 2025-04-30 17:12</li>
          <li>New client onboarded - 2025-04-29 10:44</li>
        </ul>
      </section>

      {/* API Health */}
      <section className="bg-white rounded shadow p-6">
        <h2 className="text-xl font-semibold mb-2 text-gray-800">API Health Check</h2>
        <div className="flex flex-col md:flex-row md:items-center md:space-x-8">
          <div className="mb-2 md:mb-0">
            <span className={`inline-block px-3 py-1 rounded-full text-white text-sm ${health.status === 'ok' ? 'bg-green-600' : 'bg-red-500'}`}>{health.status}</span>
          </div>
          <div>
            <span className="text-gray-500 text-sm">Timestamp: {health.timestamp}</span>
          </div>
        </div>
      </section>
    </main>
  );
}
